import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Header, Group, Cell, List, setState, Avatar, Footer, Switch } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';



const osName = platform();

const Info = ({ go,id}) => (
	<Panel id={id}>
		<PanelHeader
			left={<HeaderButton onClick={go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Классы
		</PanelHeader>
		<Group title={<Header mode="secondary">Информация о нас</Header>}>
		 <List>
		  <Cell before={<Avatar />}description="Издательский дом">ПостНаука</Cell>
                <Cell>Название: GDZ || DC </Cell>
                <cell><br></br>   |Версия: 1.1</cell>
                <br></br>
                <br></br>

                <cell> |Создатели: <br></br> |Роман Пух<br></br> |Алан Панов</cell>
              </List>

              </Group>
 <Group title={<Header mode="secondary">Настройки</Header>}>
              <List>
                <Cell onClick={go} data-to="DZ3">Учетная Запись</Cell>
                <Cell onClick={go} data-to="DZ3">Основные</Cell>
                <Cell onClick={go} data-to="DZ3">Приватность</Cell>
                  <Cell asideContent={<Switch />}>
             Test
             </Cell>
                
              </List>
               <Footer> Версия сервиса 1.1</Footer>
            </Group>





	</Panel>
);

Info.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Info;