import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Header, Group, Cell, List, Footer  } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';



const osName = platform();

const Dzalg1 = ({ go,id}) => (
	<Panel id={id}>
		<PanelHeader
			left={<HeaderButton onClick={go} data-to="dzl1">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Ю. Н. Макарычев
		</PanelHeader>
		<Group title={<Header mode="secondary">Номера</Header>}>
		 <List>
                <Cell >Номер 1</Cell>
               <img src="https://sun9-50.userapi.com/c205616/v205616509/6b64e/Zf0wws1jEBo.jpg"/>


              </List>
               <Footer> Все права защищены Daycore.ru </Footer>
		</Group>
	</Panel>
);

Dzalg1.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Dzalg1;