import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Header, Group, Cell, List } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';



const osName = platform();

const Dze = ({ go,id}) => (
	<Panel id={id}>
		<PanelHeader
			left={<HeaderButton onClick={go} data-to="persik">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			8 класс
		</PanelHeader>
		<Group title={<Header mode="secondary">Предмет</Header>}>
		 <List>
                <Cell>Русский язык</Cell>
                <Cell>Информатика</Cell>
                <Cell>Литература</Cell>
                <Cell>История России</Cell>
                <Cell>Всеобщая История</Cell>
                <Cell>Обществознание</Cell>
                <Cell>География</Cell>
                <Cell onClick={go} data-to="al1">Алгебра </Cell>
                <Cell>Геометрия</Cell>
                <Cell>Физика</Cell>
                <Cell>Биология</Cell>
                <Cell>Химия</Cell>
                <Cell>ОБЖ</Cell>
                <Cell>Английский язык</Cell>
                <Cell>Основы экономики</Cell>

              </List>
		</Group>
	</Panel>
);

Dze.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Dze;